import {
  FlatList,
  Image,
  ImageBackground,
  Linking,
  Pressable,
  ScrollView,
  TouchableOpacity,
  View,
} from 'react-native';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import TaskComponent from './component/Task';
import {DUMMY_DATA, IMenuServices, IServices} from './helper.tsx';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import React, {useEffect, useState} from 'react';
import {useAppSelector} from '../../hooks/useRedux.ts';
import NavigationName from '../../constants/NavigationName.ts';
import {
  IcCeCa,
  IceAccount,
  IceConDrive,
  IceContract,
  IceFlow,
  IceForm,
  IceRequest,
  IceTask,
  IconAccount,
  IcWaveHand,
} from '../../assets/images/index.ts';
import TextCM from '../components/Text/index.tsx';
import Color from '../../constants/Color.ts';
import {handleUrl} from './handleDeepLink.tsx';
import {useAccountInfoServices} from '../../hooks/apiHooks/useAccountInfoServices.tsx';
import HeaderCM from '../components/Header/HeaderCM.tsx';
import {Icon} from '@ui-kitten/components';
import CheckInOut from '../check-in-out/index.tsx';
import Font from '../../constants/Font.ts';
import String from '../../constants/String.ts';

const HomeScreenHostApp = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const {dataAccount} = useAppSelector(state => state.account);
  const [menuSwaps, setMenuSwaps] = useState<IMenuServices[]>([]);

  const AccountServices = useAccountInfoServices();

  useEffect(() => {
    const checkInitialURL = async () => {
      const url = await Linking.getInitialURL();
      if (url) {
        const dataFromUrl = handleUrl(url);
        if (dataFromUrl) {
          navigation.navigate(NavigationName.ERequest, dataFromUrl);
        }
      }
    };
    checkInitialURL();

    const linkingSubscription = Linking.addEventListener(
      'url',
      async ({url}) => {
        if (url) {
          const dataFromUrl = handleUrl(url);
          if (dataFromUrl) {
            navigation.navigate(NavigationName.ERequest, dataFromUrl);
          }
        }
      },
    );
    return () => linkingSubscription.remove();
  }, []);

  useEffect(() => {
    getMenuView();
  }, []);

  const getMenuView = async () => {
    const dataResp = (await AccountServices.getMenuView()) as IMenuServices[];
    if (dataResp) {
      const filterData = dataResp.map(menu => {
        return {
          ...menu,
          servicesList: menu?.servicesList?.map(service =>
            checkpathIcon(service),
          ),
        };
      });
      setMenuSwaps(filterData);
    }
  };

  const checkpathIcon = (service: IServices) => {
    switch (service.serviceName) {
      case 'switchApp.eContract.name':
        return {
          ...service,
          imgPath: <IceContract width={18} height={18} />,
          path: NavigationName.eContract,
        };
      case 'switchApp.eRequest.name':
        return {
          ...service,
          imgPath: <IceRequest width={18} height={18} />,
          path: NavigationName.ERequest,
        };
      case 'switchApp.eTask.name':
        return {
          ...service,
          imgPath: <IceTask width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eForm.name':
        return {
          ...service,
          imgPath: <IceForm width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eConDrive.name':
        return {
          ...service,
          imgPath: <IceConDrive width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eAccount.name':
        return {
          ...service,
          imgPath: <IceAccount width={18} height={18} />,
          path: null,
        };
      case 'switchApp.ceca.name':
        return {
          ...service,
          imgPath: <IcCeCa width={18} height={18} />,
          path: null,
        };
      case 'switchApp.eFlow.name':
        return {
          ...service,
          imgPath: <IceFlow width={18} height={18} />,
          path: null,
        };
      default:
        return {
          ...service,
          imgPath: <IconAccount width={18} height={18} />,
          path: null,
        };
    }
  };

  const handleGoToAccountSetting = () => {
    navigation.navigate(NavigationName.AccountSetting);
  };

  /**
   * Hàm lấy danh sách các serviceName từ list A
   */
  const getAllServiceNamesFromA = (A: any[]) => {
    const serviceNames = new Set();
    A.forEach(item => {
      item.servicesList.forEach((service: any) => {
        serviceNames.add(service.nameTranslateKey?.split('.')?.[1]); // Hoặc service.nameTranslateKey nếu phù hợp
      });
    });
    return serviceNames;
  };

  const FilteredFlatList = () => {
    // Lấy danh sách serviceName có trong A
    const serviceNamesFromA = getAllServiceNamesFromA(menuSwaps);

    // Lọc dữ liệu B
    const filteredData = DUMMY_DATA.map(section => {
      const filteredServices = section?.servicesList?.filter(service =>
        serviceNamesFromA.has(service.serviceName),
      );

      // Chỉ giữ lại section nếu có ít nhất 1 service khớp
      if (filteredServices && filteredServices?.length > 0) {
        return {
          ...section,
          servicesList: filteredServices,
        };
      }
      return null;
    }).filter(section => section !== null);
    return filteredData;
  };

  const renderNewHomeScreen = () => {
    return (
      <SafeAreaView
        edges={['bottom']}
        style={{backgroundColor: '#fff', flex: 1}}>
        <HeaderCM
          renderContentLeftDynamic={() => (
            <TouchableOpacity
              onPress={handleGoToAccountSetting}
              style={[styles.ctnLeft]}>
              <View style={styles.ctnImage}>
                <Image
                  width={40}
                  height={40}
                  source={{
                    uri: dataAccount.imageUrl || String.DEFAULT_IMAGE,
                  }}
                />
              </View>
              <View style={{rowGap: 8}}>
                <TextCM style={[styles.text, {fontSize: 16}]}>
                  {t('HO.home.hello')}
                  <IcWaveHand width={20} height={20} />
                </TextCM>
                <TextCM
                  numberOfLines={1}
                  style={[styles.text, {fontSize: 12, maxWidth: 200}]}>
                  {'' + dataAccount?.firstName + ' ' + dataAccount?.lastName}
                </TextCM>
              </View>
            </TouchableOpacity>
          )}
          title=""
          renderContentRight={() => (
            <Pressable>
              <Icon fill={'#fff'} name="bell-outline" width={24} height={24} />
            </Pressable>
          )}
        />

        <ScrollView
          style={{
            paddingHorizontal: 16,
            backgroundColor: '#fff',
          }}>
          <CheckInOut type="checkinout" />
          <TextCM
            style={{
              fontSize: 16,
              fontFamily: Font.InterSemiBold600,
              marginTop: 10,
            }}>
            Danh sách dịch vụ
          </TextCM>

          <FlatList
            scrollEnabled={false}
            data={FilteredFlatList()}
            showsVerticalScrollIndicator={false}
            renderItem={item => (
              <View key={item.item.id}>
                <TaskComponent data={item.item} />
              </View>
            )}
            ListEmptyComponent={() => (
              <View
                style={{
                  alignItems: 'center',
                  justifyContent: 'center',
                  flex: 1,
                }}>
                <TextCM>{t('CM.emptyData')}</TextCM>
              </View>
            )}
          />
        </ScrollView>
      </SafeAreaView>
    );
  };

  return <>{renderNewHomeScreen()}</>;
};

export default HomeScreenHostApp;
