import {Alert, FlatList, Image, TouchableOpacity, View} from 'react-native';
import styles from './styles';
import {FC} from 'react';
import {useNavigation} from '@react-navigation/native';
import {useTranslation} from 'react-i18next';
import {IIconHome, IMenuServices, IServices, ITask} from '../../helper';
import TextCM from '../../../components/Text';

type Props = {
  data: IMenuServices;
};

const listShow = ['eContract', 'eTask', 'eRequest', 'eBienLai'];

const TaskComponent: FC<Props> = ({data}) => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();

  const renderItemList = ({item}: {item: IServices}) => {
    return (
      <>
        {listShow.includes(item.nameTranslateKey) && (
          <TouchableOpacity
            onPress={() =>
              item.path
                ? navigation.navigate(item.path)
                : Alert.alert(t('CM.noti'), t('NT.notification.inprocess_noti'))
            }
            style={styles.ctnIcon}>
            <View style={styles.viewIcon}>{item.imgPath}</View>
            <TextCM>{item?.nameTranslateKey}</TextCM>
          </TouchableOpacity>
        )}
      </>
    );
  };

  return (
    <View key={data.id} style={styles.ctnContainer}>
      <TextCM style={styles.TextTitle}>{t((data as any)?.name)}</TextCM>
      <FlatList
        nestedScrollEnabled
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        horizontal
        data={data.servicesList}
        renderItem={renderItemList}
      />
    </View>
  );
};

export default TaskComponent;
